body {
  --header-height: 60px;
  --nav-item-color: #000;
  --nav-item-color-hover: #00acdc;
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

.header {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__logo-wrapper {
  height: 40px;
}

.header__logo {
  object-fit: cover;
  width: 40px;
  height: 40px;
}

.nav__list {
  list-style: none;
  display: flex;
}

.nav__list-item:nth-child(n+2) {
  margin-left: 20px;
}

.nav__list-link {
  color: var(--nav-item-color);
  text-transform: uppercase;
  height: var(--header-height);
  font-family: Roboto, serif;
  font-size: 12px;
  font-weight: 500;
  line-height: var(--header-height);
  text-decoration: none;
  transition: all .2s;
  display: inline-block;
  position: relative;
}

.is-active:link, .nav__list-link:visited, .nav__list-link:hover {
  color: var(--nav-item-color-hover);
}

.nav__list-link:active:after, .is-active:after {
  content: " ";
  background-color: var(--nav-item-color-hover);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  right: 0;
}
